              </svg>
            </div>
            <div class="msg">
              <span>{{ flashMessage.message | trans }}</span>
            </div>
            <div @click="$store.commit('SET_FLASH_MESSAGE', {})">
              <svg
                version="1.1"
                id="Layer_1"
                xmlns="http://www.w3.org/2000/svg"
